<template>
  <div>
    <div v-if="channel === 0" class="contact-container">
      <p class="client-title">{{$t('help/contact-us.Title')}}</p>
      <p class="client-description">{{$t('help/contact-us.Subtitle')}}</p>
      <div class="channel-item-container" v-if="isPc">
        <div class="channel-item">
          <i class="channel-icon"></i>
          <p class="channel-title">{{$t('help/contact-us.Bullet-1')}}</p>
          <p class="channel-description">{{$t('help/contact-us.Paragraph-1')}}</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openZw()">{{$t('help/contact-us.Button-1')}}</a-button>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-email-icon"></i>
          <p class="channel-title">{{$t('help/contact-us.Bullet-2')}}</p>
          <p class="channel-description">{{$t('help/contact-us.Paragraph-2')}}</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openNewRequest()">{{$t('help/contact-us.Button-2')}}</a-button>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-phone-icon"></i>
          <p class="channel-title">{{$t('help/contact-us.Bullet-3')}}</p>
          <p class="channel-description">{{$t('help/contact-us.Paragraph-3')}}</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openZw()">{{$t('help/contact-us.Button-3')}}</a-button>
        </div>
      </div>
      <div class="channel-item-container" v-if="!isPc">
        <div class="channel-item">
          <i class="channel-icon"></i>
          <div class="channel-item-right">
            <p class="channel-title">{{$t('help/contact-us.Bullet-1')}}</p>
            <p class="channel-description">{{$t('help/contact-us.Paragraph-1')}}</p>
            <span class="bottom-description" @click="openZw()">{{$t('help/contact-us.Button-1')}}</span>
          </div>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-email-icon"></i>
          <div class="channel-item-right">
            <p class="channel-title">{{$t('help/contact-us.Bullet-2')}}</p>
            <p class="channel-description">{{$t('help/contact-us.Paragraph-2')}}</p>
            <span class="bottom-description" @click="openNewRequest()">{{$t('help/contact-us.Button-2')}}</span>
          </div>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-phone-icon"></i>
          <div class="channel-item-right">
            <p class="channel-title">{{$t('help/contact-us.Bullet-3')}}</p>
            <p class="channel-description">{{$t('help/contact-us.Paragraph-3')}}。</p>
            <span class="bottom-description" @click="openZw()">{{$t('help/contact-us.Button-3')}}</span>
          </div>
        </div>
      </div>
    </div>
    <div v-if="channel === 1" class="contact-email">
      <p class="client-title">{{$t('help/contact-us:ticket.Title')}}</p>
      <a-form class="email-form" :form="form" @submit="handleSubmit" layout="vertical">
        <a-form-item :label="$t('help/contact-us:ticket.Bullet-1')" class="form-item">
          <a-input class="form-email" :placeholder="$t('help/contact-us:ticket.Text-1')"
                   v-decorator="['email', { rules: [{ required: true, message: $t('help/contact-us:ticket.Text-1') }],initialValue: email }]"
          />
        </a-form-item>
        <a-form-item :label="$t('help/contact-us:ticket.Bullet-2')" class="form-item">
          <a-textarea class="form-description" :placeholder="$t('help/contact-us:ticket.Text-2')" :autoSize="{ minRows: 8, maxRows: 8}"
                      v-decorator="['detail', { rules: [{ required: true, message: $t('help/contact-us:ticket.Text-2') }] }]"
          />
        </a-form-item>
        <a-form-item :label="$t('help/contact-us:ticket.Bullet-3')" class="form-item">
          <div class="dropbox">
            <a-upload-dragger
                    class="form-upload-dragger"
                    v-decorator="['dragger',{valuePropName: 'fileList',getValueFromEvent: normFile,},]"
                    name="uploaded_data"
                    :action="uploadUrl"
                    :data="uploadData"
                    @change="handleChange"
                    :beforeUpload="uploadFileCheck"
            >
              <p class="ant-upload-drag-icon">
                <i class="form-upload-icon"></i>
              </p>
              <p class="ant-upload-text">
                {{isPc ? $t('help/contact-us:ticket.Bullet-4') : $t('help/contact-us:ticket.Bullet-3')}}
              </p>
              <p class="ant-upload-hint" v-if="isPc">
                {{$t('help/contact-us:ticket.Text-3')}}
              </p>
            </a-upload-dragger>
          </div>
        </a-form-item>
        <a-form-item class="form-item-btn">
          <a-button class="submit-btn" type="primary" html-type="submit" :loading="submitLoading">
            {{$t('help/contact-us:ticket.Button')}}
          </a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SupportContactUsView',
    data() {
      return {
        uploadUrl: this.$zUrl + '/api/v2/uploads',
        isPc: this.$isPc(),
        channel: 0,
        uploadData: {
          filename: ''
        },
        formLayout: 'horizontal',
        form: this.$form.createForm(this, { name: 'coordinated' }),
        uploadFIleIds: [],
        submitLoading: false,
        maxFiles: 5,
        email: localStorage.getItem("email")
      };
    },
    props : {
      channelId: {},
    },
    watch: {
      channelId: {
        immediate: true,
        handler(value) {
          if (JSON.stringify(value) !== '{}') {
            this.channel = value.channelId;
          }
        }
      }
    },
    methods: {
      openZw() {
        window.zE('webWidget', 'open');
        setTimeout(function () {
          // Open the channel
          document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
        }, 1000);
      },
      openNewRequest() {
        this.channel = 1;
      },
      handleSubmit(e) {
        e.preventDefault();
        this.form.validateFields((err, values) => {
          if (!err) {
            this.submitLoading = true;
            // console.log('Received values of form: ', values);
            let data = {
              'request': {
                'requester': {
                  'name': values['email'],
                  'email': values['email']
                },
                'subject': values['detail'],
                'comment': {
                  'body': `${values['detail']}↵↵---------- ↵ 來自${window.location.href}`,
                  'uploads': this.uploadFIleIds
                }
              }
            };
            const that = this;
            this.$request.postNewRequest(data, function (data) {
              that.submitLoading = false;
              if ((data.status === 201 || data.status === 200) && data.request.id != 0) {
                that.channel = 0;
                that.$notification.open({
                  message: this.$t('help/contact-us:ticket.Notice-1') + '\n' +
                          this.$t('help/contact-us:ticket.Notice-2'),
                  onClick: () => {
                  },
                });
              } else {
                that.$notification.open({
                  message: this.$t('help/contact-us:ticket.Warning-1'),
                  onClick: () => {
                  },
                });
              }
            });
          }
        });
      },
      handleChange(info) {
        // console.log(info)
        const status = info.file.status;
        if (status === 'done') {
          this.$message.success(`${info.file.name}` + this.$t('help/contact-us:ticket.Notice-3'));
          this.uploadFIleIds.push(info.file.response.upload.token);
        } else if (!status) {
          this.$message.error(`${info.file.name}` + this.$t('help/contact-us:ticket.Notice-4'));
        }


      },
      normFile(e) {
        if (Array.isArray(e)) {
          return e;
        }
        return e && e.fileList;
      },
      uploadFileCheck() {
        return this.uploadFIleIds.length < this.maxFiles;
      }
    }
  };
</script>

